import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import WebFont from 'webfontloader';
import cn from 'classnames';

import styles from './SelectFont.scss';
import { useTranslation } from 'react-i18next';

const defaultFonts = {
    google: [
        'Alice',
        'Amatic SC',
        'Caveat',
        'Comfortaa',
        'Cormorant Garamond',
        'Cormorant',
        'IBM Plex Sans',
        'Inter',
        'Kelly Slab',
        'Lobster',
        'Manrope',
        'Merriweather',
        'Montserrat',
        'Montserrat Alternates',
        'Noto Sans',
        'Noto Serif',
        'Open Sans',
        'Oswald',
        'Pacifico',
        'Pangolin',
        'Playfair Display',
        'PT Mono',
        'PT Sans',
        'PT Serif',
        'Raleway',
        'Roboto Condensed',
        'Roboto Slab',
        'Roboto', 'Rubik',
        'Russo One',
        'Source Code Pro',
        'Ubuntu',
        'Unbounded',
    ],
    websafe: [
        'Arial',
        'Comic Sans MS',
        'Georgia',
        'Helvetica',
        'Impact',
        'Times New Roman',
    ],
};

const SelectFont = ({ fonts = defaultFonts, onChange, name, currentFontFamily }) => {
    const { t } = useTranslation('ui');
    const options = [
        ...fonts.google.map((font) => ({ family: font, provider: 'google' })),
        ...fonts.websafe.map((font) => ({ family: font, provider: 'websafe' })),
    ].sort((a, b) => a.family < b.family ? -1 : a.family > b.family ? 1 : 0);

    useEffect(() => {
        WebFont.load({
            google: {
                families: fonts.google,
            },
        });
    }, []);

    return (
        <>
            <ul className={styles.list}>
                {options.map((font, index) => {
                    const isActive = font.family === currentFontFamily;

                    return (
                        <li
                            key={index}
                            className={cn(styles.item, isActive && styles.active)}
                            style={{ fontFamily: font.family }}
                            onClick={() => onChange(name, font)}
                        >
                            <h2 className={styles.label}>{font.family}</h2>
                            <p className={styles.subLabel}>{t('SelectFont.example_text')}</p>
                        </li>
                    );
                })}
            </ul>
        </>
    );
};

SelectFont.propTypes = {
    fonts: PropTypes.array,
    onChange: PropTypes.func.isRequired,
    name: PropTypes.string.isRequired,
    currentFontFamily: PropTypes.string,
};

export default SelectFont;
